            <v-text-field label="Search log" dense solo flat color="neon" hide-details v-model="log_search"
              style="background: transparent;flex:1" prepend-inner-icon="mdi-magnify" height="40"></v-text-field>
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
