                  class="mr-2">Save Log <v-icon class="ml-1" size="18">mdi-chevron-down</v-icon></v-btn>
              </template>
              <v-list dense>
                <v-list-item @click="SaveLog(focused_log)" v-if="focused_log !== null">Save {{ focused_log    }}</v-list-item>
                <v-list-item @click="SaveLog()">Save All Log</v-list-item>
