<script>
import "@/style.scss"
const params = new URL(window.location).searchParams
const procedure = async (data) => {
  try {
    await window.chrome.webview.postMessage(data)
  }
  catch (ex) {
    console.log(ex)
  }
}
const routines = [
  {
    category: "Simple Task",
    list: [
      "Spin Coin",
      "Synth Gems",
      "Opening Pack"
    ]
  },
  {
    category: "Dailies",
    list: [
      "Corruption",
      "Ladder",
      "Bath",
    ]
  },
  {
    category: "Grind Gold",
    list: [
      "310", "320", "340", "355"]
  },
  {
    category: "Dungeon",
    list: [
      "30",
      "45",
      "55",
      "75",
      "85",
      "110",
      "125",
      "165",
      "175",
      "195",
      "215",
      "235",
      "255",
      "275",
      "295",
      "315",
      "335",
      "355",
      "375",
      "395",
      "415",
      "Pixie"
    ]
  }
]


export default {
  data: () => ({
    ready: false,
    is_macro_running: false,
    routines,
    search_routine: "",
    shortcuts: [
      { category: "Dungeon", name: "85" }
    ],
    selected_macro: "",
    workspace_mode: params.get("workspace_mode") || 'freemode',
    flash_hwnd: params.get("flash_hwnd") || 0,

    log_row_limit: 300,
    log_category: ['Event', 'Image', 'Error', 'Macro', 'Test'],
    log_tabs_icon: ['mdi-book-clock-outline', 'mdi-image-outline', 'mdi-alert-circle-outline', 'mdi-robot-outline', 'mdi-progress-check'],
    log_tab: 0,
    log_search: "",
    logs: [],
    focused_log: null,
    focused_log_index: -1,
    auto_scroll_to_buttom: true


  }),
  watch: {
    focused_log(n) {
      console.log("fl", n)
      let i = this.logs.findIndex((l) => l.name == n)
      if (i !== -1) {
        this.focused_log_index = i
      }
      console.log("i is", i)
    }
  },
  computed: {
    all_log() {
      let result = []
      // console.log()
      Object.keys(this.logs[this.focused_log_index].records).forEach(k => {
        result.push(...this.logs[this.focused_log_index].records[k])
      })
      console.log("rres", result)
      return result.filter((r) => `${r.timestamp} ${r.description}`.toLowerCase().includes(this.log_search.toLocaleLowerCase()))
    },
    filtered_routines: function () {
      console.log(this.routines)
      let r = this.routines.map((r) => ({
        category: r.category,
        list: r.list.filter(l => l.toLowerCase().includes(this.search_routine))
      })).filter((r) => r.list.length)
      console.log(r)
      return r
    }
  },

  methods: {

    SetActiveMacro(routine, category = null) {
      this.is_macro_running = true
      this.selected_macro = routine
      const log_section = {
        name: `${new Date().toString().slice(16, 24)} - ${category == "Dungeon" ? "Dungeon" : ''} ${routine}`,
        timestamp: new Date().toString().slice(16, 24),
        routine_name: routine,
        records: {}

      }
      this.log_category.forEach(lc => {
        log_section.records[lc] = []
      })

      this.logs.push(log_section)
      this.focused_log = log_section.name
      this.ready = true
    },

    CopyFlashHWND() {
      navigator.clipboard.writeText(this.flash_hwnd)
    },
    Screenshot() {
      procedure({ Event: "screenshot", Data: null })
    },

    Automate() {
      this.SetActiveMacro("Automate")
      procedure({ Event: "run_macro", Data: `..\\automate` })
    },
    Test() {
      this.SetActiveMacro("Test")
      procedure({ Event: "test" })
    },

    SaveLog(log = null) {
      var textToSave = JSON.stringify(this.logs, null, 2)
      if (log !== null) {
        let idx = this.logs.findIndex(l => l.name == log)
        if (idx !== -1) {
          textToSave = JSON.stringify(this.logs[idx], null, 2)
        } else {
          return alert("Invalid Index")
        }

      }

      var hiddenElement = document.createElement('a');
      hiddenElement.href = 'data:attachment/json,' + encodeURI(textToSave);
      hiddenElement.target = '_blank';
      hiddenElement.download = `CS ${log || new Date().getTime()}.json`;
      hiddenElement.click();
    },
    Log(log_obj) {
      let log_index = this.logs.length - 1
      if (log_index !== -1) {
        let category = log_obj.category
        delete log_obj.category
        this.logs[log_index].records[category].push({
          ...log_obj,
          timestamp: new Date().toString().slice(15, 24)
        })
        this.ScrollToLatest()
      }
    },
    ScrollToLatest() {
      if (this.auto_scroll_to_buttom) {
        var objDiv = document.getElementById("console-ui");
        setTimeout(() => {
          objDiv.scrollTop = objDiv.scrollHeight;
        }, 300)
      }
    },
    RunMacro(routine, category = null) {
      console.log(category, routine)
      this.SetActiveMacro(routine, category)
      procedure({ Event: "run_macro", Data: encodeURI(`${category}/${routine}`) })
    },
    StopMacro() {
      this.is_macro_running = false
      this.selected_macro = ""
      procedure({ Event: "stop_macro", Data: null })
    }
  },
  mounted() {
    window.$com = this
    this.SetActiveMacro("Logg")
  }

}
</script>